	module.exports = {"data":{"markdownRemark":{"html":"<h2>Integration Rules</h2>\n<p>As with differentiation, we use a variety of rules when integrating functions. Below are a few of the most common.</p>\n<p><strong>The Constant Rule:</strong>\n∫k dx = kx + C   where k is a constant</p>\n<p><strong>The Power Rule:</strong>\n∫ x<sup>n</sup> dx =  + <sup>x<sup>n+1</sup></sup>⁄<sub>n+1</sub> + C when n ≠ 1         </p>\n<p><strong>Exponential Rule:</strong>\n∫ e<sup>kx</sup> dx = <sup>1</sup>⁄<sub>kx</sub>  e<sup>kx</sup> + C  where k is a constant</p>\n<p><strong>Trigometric Rules:</strong></p>\n<p>∫ cos(x) dx = sin(x) + C</p>\n<p>∫ sin(x) dx = -cos(x) + C</p>\n<p><strong>Sum/Difference Rules:</strong></p>\n<p>∫[f(x) + g(x)]dx = ∫f(x)dx + ∫g(x)dx</p>\n<p>∫[f(x) - g(x)]dx = ∫f(x)dx - ∫g(x)dx</p>\n<h4>More Information:</h4>\n<p><a href=\"http://archive.learnhigher.ac.uk/resources/files/Numeracy/Integration_webversion.pdf\">Basic Rules with Examples</a></p>\n<p><a href=\"http://tutorial.math.lamar.edu/pdf/Calculus_Cheat_Sheet_Integrals.pdf\">In Depth Guide to Integrals</a></p>","fields":{"slug":"/mathematics/integration-rules/"},"frontmatter":{"title":"Integration Rules"}}},"pathContext":{"slug":"/mathematics/integration-rules/"}}

